.cell {
    width: 93px;
    height: 91px;
    display: flex;
    align-items: center;
    justify-content: center;

    .piece {
        width: 40px;
        height: 75px; } }

.from-cell {
    background-color: #00ff0033; }

.target-cell {
    background-color: #0000ff33; }

.player-panel {
    width: 512px;
    height: 188px;

    .card-container {}

    .card {
        width: 215px;
        height: 100px;

        margin-left: 10px;
        margin-right: 10px;
        border: 3px;
        border-style: solid;
        border-color: black; }

    .selected-card {
        border-color: blue; }

    .mini-card {
        width: 112px;
        height: 57px;
        margin-top: 5px;
        margin-bottom: 5px;
        margin-left: 370px; }

    .cards {
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: flex-end; } }

.opponent-player-panel {
    transform: rotate(180deg);
    transform-origin: center; }

.field-panel {
    width: 512px;
    height: 833px;
    background-image: url("../img/board.jpg");
    background-size: 100%;

    .field {
        margin-left: 25px;

        .field-rows {
            display: flex;
            flex-direction: column; }

        .field-columns {
            display: flex;
            flex-direction: row; } } }
