@import "variables.sass";
@import "elements.sass";
@import "panels.sass";
@import "views.sass";

html,
body {
    width: 100%;
    height: 100%;
    background: black;
    margin: 0;
    overflow: hidden;
    color: white;
    position: relative;

    * {
        font-family: 'Source Sans Pro', sans-serif;
        text-shadow: none;
        user-select: none;
        border: 0 none;
        color: white; }

    display: flex;
    justify-content: center;
    align-items: center; }
